import { CommonModule } from '@angular/common'
import {
  Component,
  ElementRef,
  Inject,
  Input,
  input,
  OnInit,
  ViewChild,
} from '@angular/core'
import { Router } from '@angular/router'
import {
  IonHeader,
  IonTitle,
  IonToolbar,
  IonBackButton,
  IonButtons,
  IonButton,
  IonIcon,
  IonImg,
  IonPopover,
  IonContent,
  IonList,
  IonItem,
  IonLabel,
  PopoverController,
} from '@ionic/angular/standalone'
import { addIcons } from 'ionicons'
import { arrowBackOutline, caretBack } from 'ionicons/icons'
import { StorageService } from 'src/app/services/utils/storage.service'
import { TranslateService } from '@ngx-translate/core'
import { LanguageService } from 'src/app/services/utils/language.service'
import { LanguagePopoverComponent } from './language-popover/language-popover.component'

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss'],
  imports: [
    IonHeader,
    CommonModule,
    IonTitle,
    IonToolbar,
    IonBackButton,
    IonButtons,
    IonIcon,
    IonButton,
    IonImg,
  ],
})
export class HeaderComponent implements OnInit {
  public program: any
  public showBackButton: boolean = true
  @Input() defaultHref: string
  constructor(
    @Inject(StorageService) private storageService: StorageService,
    private router: Router,
    public language: LanguageService,
    private popoverController: PopoverController
  ) {
    addIcons({
      'flag-fr': 'assets/svg/flags/fr.svg',
      'flag-uk': 'assets/svg/flags/uk.svg',
      'flag-de': 'assets/svg/flags/de.svg',
      'flag-it': 'assets/svg/flags/it.svg',
      'flag-es': 'assets/svg/flags/es.svg',
      caretBack,
      arrowBackOutline,
    })
    this.router.url.includes('landing')
      ? (this.showBackButton = false)
      : (this.showBackButton = true)
  }

  async ngOnInit() {
    await this.storageService.get('program').then((program) => {
      this.program = program
    })
  }

  changeLanguage(lang: string) {
    this.language.setLanguage(lang)
  }

  async openLanguagePopover(event: any) {
    const popover = await this.popoverController.create({
      component: LanguagePopoverComponent,
      event,
    })
    popover.onDidDismiss().then((data) => {
      if (data.data) {
        this.changeLanguage(data.data.language)
      }
    })
    await popover.present()
  }
}
